.App {
  text-align: center;
}
.togglein{
  animation: toggleIn 1s 1;
}

@keyframes toggleIn {
  0% {
    right:100vw;
  }
  100% {
    right:0px;
  }
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.dot {
  height: 25px;
  width: 25px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}

.maindiv{
  width:80vw;
}
html{
  background-color: #d2dfff;
}
.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(71, 71, 71);
}

.quizdiv{
  display:flex;
  justify-content: center;
  width:100%;
}
.quizdiv > div{
  width:33%
}
.green{
  background-color:rgb(218, 255, 232);
}
.red{background-color: inherit;}
.resultentry{
  display:flex;
  width:100%;
  align-items: center;
  margin: 20px 0px;
  
}
.resultentry > h2{
  padding: 0px 20px;
  width: 100%;
}
.resultimg{
  width: 175px;
  height: 175px;
  margin-right: 10px;
}
.resultval{
  flex-grow: 1;
}
.startbtn{
  color:#585e70;
  font-size: 40px;
  width:100%;
  cursor: pointer;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  border: none;
  transition: 0.3s;
}
.startbtn:hover{
  background-color: rgb(255, 245, 245);
}
.startscreen{
  animation: toggleIn 1s 1;
  position: relative;
  display: flex;
  flex-direction: column;
}
.startscreen>button{
  margin-top: 10%;
  padding: 10px 0px;
}
.genres{
  display:flex;
  flex-direction: column;
}
.genrebutton>button{
  cursor: pointer;
  width:60%;
  padding: 10px 0px;
  font-size: 20px;
  text-transform: capitalize;
  border: 2px solid white;
  border-radius: 10px;
  color:rgb(136, 136, 136);
  transition: 0.3s;
}
.genrebutton>button:hover{
 cursor: pointer;
}
.genrebutton{
  padding: 10px 0px;
}
.genresubmitbtn:hover{
  background-color: white;
}
.errorgenre{
  color:rgb(255, 112, 112);
}
.guesssubmitbtn{
  cursor: pointer;
  width:40%;
  padding: 10px 0px;
  font-size: 20px;
  border: none;
  border-radius: 10px;
  color:rgb(136, 136, 136);
  transition: 0.3s;
}
.guesssubmitbtn:hover{
  background-color: white;
}
.guesssubmit{
  margin: 10px 0px;
}
.guessinputfield{
  background-color: inherit;
  border: none;
  border-bottom:2px solid white;
  width: 40%;
  font-size: 20px;
  color: rgb(92, 92, 92);
  transition: 0.2s;
}
.guessinputfield:focus{
  outline: none;
  border-bottom:2px solid rgb(102, 102, 102);
}
.guessinput{
  margin: 15px 0px;
}
.restart{
  background-color: rgb(251, 248, 255);
  cursor: pointer;
  margin: 20px 0px;
  padding: 0px 0px;
  border-radius: 10px;
  color:rgb(54, 54, 54);
  transition: 0.3s;
}
.restart:hover{
  background-color: rgb(223, 255, 244);
}

.resulticon{
  height: 100px;
  width: 100px;
  float:right;
}
.resulticoncontainer{
  flex-grow: 1;
  display:flex;
  justify-content: right;
  
}
.resulticoncontainer > div{
  width:100px;
}